.weather {
    place-self: center;
    padding: 40px;
    border-radius: 10px;
    background-image: linear-gradient(45deg, #2f4680, #500ae4);
}
.search-bar {
    display: flex;
    justify-content: space-between;
    gap: 12px;
    align-items: center;
}
.search-bar-input {
    height: 50px;
    border: none;
    outline: none;
    border-radius: 40px;
    padding-left: 25px;
    color: #626262;
    background: #ebfffc;
    font-size: 18px;
    flex: 1;
}
.search-icon {
    .search-icon {
        font-size: 30px;  
        color: black;    
        cursor: pointer;  
        margin-left: 10px;
      }
      
}
.weather-icon{
    margin: 30px 0px;
    width: 60px;
}
.temperature {
    font-size: 80px;
    color: #fff;
    line-height: 1;
    }
.location{
    font-size: 40px;
    color: #fff;
}
.weather-data{
    width: 100%;
    margin-top: 40px;
    color: #fff;
    display: flex;
    justify-content: space-between;
}
.weather-data.col{
    display: flex;
    align-items: flex-start;
    gap:12px;
    font-size: 22px;
}
.weather-data.col span{
    font-size: 16px;
     display: block;
}
.weather-data.col img{
    width: 26px;
    margin-top: 10px;
}